@import url('https://fonts.cdnfonts.com/css/glober');

* {
  font-family: 'Glober Demo', sans-serif;
  background-color: rgb(255, 255, 255);
  color: black;
}

.App {
  text-align: center;
}

.App h1 {
  text-align: center;
}

/* HEADER */

.header {
  padding: 20px;
  display: flex;
  justify-content: safe center;
}

.header h1 {
  font-size: 50px;
}

#header-logo {
  width: 150px;
  margin-left: 50px;
  margin-right: 50px;
}

.header a {
  text-decoration: none;
  color: black;
}

/* NAVBAR */
.navbar ul {
  display: flex;
  justify-content: safe center;
}

.navbar li {
  list-style: none;
  margin-left: 20px;
  margin-right: 20px;
}

.navbar li a:hover {
  color: rgb(169, 169, 169);
}

.active {
  color: rgb(169, 169, 169);
}

/* FOOTER */

/* HOME */
.home {
  display: flex;
  justify-content: safe center;
  margin-top: 50px;
}

.home img {
  width: 20%;
  border-radius: 50%;
  padding: 50px;
}

.home-intro{
  padding-top: 100px;
  padding-bottom: 50px;
}

